import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	InspectorControls,
} from '@wordpress/block-editor';
import {
	PanelBody,
	RangeControl,
} from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

export default function Edit( { attributes, setAttributes } ) {
	const { postCount } = attributes;

	const blockProps = useBlockProps();

	return (
		<>
			<InspectorControls>
				<PanelBody
					title={ __( 'Latest Posts Settings', 'pureblocks' ) }
					initialOpen={ true }
				>
					<RangeControl
						label={ __( 'Number of posts', 'pureblocks' ) }
						value={ postCount }
						onChange={ ( value ) =>
							setAttributes( { postCount: value } )
						}
						min={ 1 }
						max={ 10 }
					/>
				</PanelBody>
			</InspectorControls>
			<div { ...blockProps }>
				<ServerSideRender
					block="pureblocks/latest-posts"
					attributes={ attributes }
				/>
			</div>
		</>
	);
}


